import React, { createContext, Suspense } from "react";
import Zizhujian from "./zizhujian";
import { Button } from "antd";
export const GuaContext: any = createContext(null);

const App = () => {
  const obj = {
    prop: 42,
    data: {
      num: 99,
    },
  };
  console.log("666666666666666666");

  Object.freeze(obj);

  obj.data.num = 33;
  // Throws an error in strict mode

  console.log(obj.data.num);
  // Expected output: 42
  const Dome3 = () => {
    console.log("====================================");
    console.log("dome3测试");
    console.log("====================================");
    return (
      <>
        <div>dome3测试</div>
      </>
    );
  };
  const Dome4 = () => {
    console.log("====================================");
    console.log("dome4测试");
    console.log("====================================");
    return (
      <>
        <div>dome4测试</div>
      </>
    );
  };
  const nestedArray = [1, [2, [3, 4], 5], 6];

  const DiArray = (arr: any[]) => {
    let NewArray: any[] = [];
    for (let i = 0; i < arr.length; i++) {
      if (Array.isArray(arr[i])) {
        NewArray = NewArray.concat(DiArray(arr[i]));
      } else {
        NewArray.push(arr[i]);
      }
    }
    return NewArray;
  };
  console.log(DiArray(nestedArray));

  const BTn = () => {
    new Array(100000).fill(1);
    new Array(20000).fill(1);
  };
  return (
    <>
      <div>
        <div>````````````````````````````</div>
        <Suspense fallback={<div>Loading...</div>}>
          <Zizhujian />
        </Suspense>
      </div>

      <Dome3 />

      <Dome4></Dome4>
      <Button onClick={BTn}>测试</Button>
    </>
  );
};

export default App;
